import React from 'react';
import { Box, Typography } from '@mui/material';
import styled from 'styled-components';

const LogoContainer = styled(Box)`
  display: flex;
  align-items: center;
  gap: 10px;
`;

const CatEmoji = styled(Typography)`
  font-size: 2rem;
  animation: float 3s ease-in-out infinite;

  @keyframes float {
    0% { transform: translateY(0px); }
    50% { transform: translateY(-5px); }
    100% { transform: translateY(0px); }
  }
`;

const WeatherCatLogo = ({ darkMode }) => {
  return (
    <LogoContainer>
      <CatEmoji>🐱</CatEmoji>
      <Typography 
        variant="h4" 
        component="span"
        sx={{ 
          fontWeight: 600,
          background: darkMode 
            ? 'linear-gradient(45deg, #89CFF0, #BFE6FF)'
            : 'linear-gradient(45deg, #5B8FA8, #89CFF0)',
          backgroundClip: 'text',
          WebkitBackgroundClip: 'text',
          color: 'transparent',
          textShadow: darkMode 
            ? '0 2px 4px rgba(0, 0, 0, 0.3)'
            : '0 2px 4px rgba(91, 143, 168, 0.2)'
        }}
      >
        Weather Cats
      </Typography>
    </LogoContainer>
  );
};

export default WeatherCatLogo; 