import React, { useState, useEffect } from 'react';
import {
  Box,
  Paper,
  Typography,
  List,
  ListItem,
  ListItemText,
  ListItemIcon,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Alert,
  CircularProgress,
  Button,
  Pagination,
  Rating
} from '@mui/material';
import styled from 'styled-components';
import StorefrontIcon from '@mui/icons-material/Storefront';
import DirectionsIcon from '@mui/icons-material/Directions';
import LocationOnIcon from '@mui/icons-material/LocationOn';
import PhoneIcon from '@mui/icons-material/Phone';
import AccessTimeIcon from '@mui/icons-material/AccessTime';
import MyLocationIcon from '@mui/icons-material/MyLocation';
import { searchPetStores, getStoreDetails } from '../../api/foursquare';
import StorePhotoGrid from './StorePhotoGrid';

const StoreContainer = styled(Paper)`
  padding: 2rem;
  margin: 0;
  background: ${props => props.$darkMode ? 'rgba(45, 45, 45, 0.9)' : 'rgba(255, 255, 255, 0.9)'};
  border-radius: 15px;
  backdrop-filter: blur(10px);
`;

const LoadingContainer = styled(Box)`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 2rem;
  gap: 1rem;
`;

const PaginationContainer = styled(Box)`
  display: flex;
  justify-content: center;
  margin-top: 2rem;
  padding-top: 1rem;
  border-top: 1px solid ${props => props.$darkMode ? 'rgba(255, 255, 255, 0.1)' : 'rgba(0, 0, 0, 0.1)'};
`;

const DetailDialog = styled(Dialog)`
  .MuiDialog-paper {
    background: ${props => props.$darkMode ? 'rgba(45, 45, 45, 0.95)' : 'rgba(255, 255, 255, 0.95)'};
    backdrop-filter: blur(10px);
    border-radius: 15px;
    padding: 1rem;
  }
`;

const PetStoreLocator = ({ darkMode }) => {
  const [stores, setStores] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [selectedStore, setSelectedStore] = useState(null);
  const [dialogOpen, setDialogOpen] = useState(false);
  const [userLocation, setUserLocation] = useState(null);
  const [page, setPage] = useState(1);
  const storesPerPage = 9;

  const fetchNearbyStores = async (latitude, longitude) => {
    try {
      const data = await searchPetStores(latitude, longitude);
      const storesData = await Promise.all(data.results.map(async (store) => {
        const details = await getStoreDetails(store.fsq_id);
        
        return {
          id: store.fsq_id,
          name: store.name,
          address: [
            store.location.address,
            store.location.locality,
            store.location.region,
            store.location.postcode
          ].filter(Boolean).join(', '),
          phone: details.phone,
          rating: details.rating ? details.rating / 2 : null,
          hours: details.hours?.display || null,
          distance: (store.distance / 1000).toFixed(1),
          latitude: store.geocodes.main.latitude,
          longitude: store.geocodes.main.longitude,
          website: details.website,
          status: details.hours?.status || null,
          popularHours: details.popular,
          tips: details.tips?.groups?.[0]?.items || [],
          categories: store.categories.map(cat => cat.name)
        };
      }));

      setStores(storesData);
      setLoading(false);
    } catch (err) {
      console.error('Error fetching stores:', err);
      setError('Unable to fetch nearby pet stores. Please try again later.');
      setLoading(false);
    }
  };

  useEffect(() => {
    const getUserLocation = () => {
      if (navigator.geolocation) {
        navigator.geolocation.getCurrentPosition(
          (position) => {
            const { latitude, longitude } = position.coords;
            setUserLocation({ latitude, longitude });
            fetchNearbyStores(latitude, longitude);
          },
          (error) => {
            console.error('Error getting location:', error);
            setError('Unable to get your location. Please enable location services to find nearby pet stores.');
            setLoading(false);
          }
        );
      } else {
        setError('Geolocation is not supported by your browser.');
        setLoading(false);
      }
    };

    getUserLocation();
  }, []);

  const handleGetDirections = (store) => {
    window.open(
      `https://www.google.com/maps/dir/?api=1&destination=${store.latitude},${store.longitude}`,
      '_blank'
    );
  };

  const handleCall = (store) => {
    window.location.href = `tel:${store.phone}`;
  };

  // Calculate pagination
  const pageCount = Math.ceil(stores.length / storesPerPage);
  const displayedStores = stores.slice(
    (page - 1) * storesPerPage,
    page * storesPerPage
  );

  if (loading) {
    return (
      <StoreContainer $darkMode={darkMode}>
        <LoadingContainer>
          <CircularProgress />
          <Typography>Finding Pet Stores Near You...</Typography>
        </LoadingContainer>
      </StoreContainer>
    );
  }

  if (error) {
    return (
      <StoreContainer $darkMode={darkMode}>
        <Alert severity="error" sx={{ mb: 2 }}>{error}</Alert>
        <Button
          variant="contained"
          startIcon={<MyLocationIcon />}
          onClick={() => {
            setLoading(true);
            setError(null);
            if (userLocation) {
              fetchNearbyStores(userLocation.latitude, userLocation.longitude);
            }
          }}
        >
          Retry
        </Button>
      </StoreContainer>
    );
  }

  return (
    <StoreContainer $darkMode={darkMode}>
      <Typography variant="h4" gutterBottom sx={{ color: darkMode ? '#fff' : '#333' }}>
        Pet Stores Near You
      </Typography>

      <StorePhotoGrid
        stores={displayedStores}
        onSelect={(store) => {
          setSelectedStore(store);
          setDialogOpen(true);
        }}
        onGetDirections={handleGetDirections}
        onCall={handleCall}
        darkMode={darkMode}
      />

      {pageCount > 1 && (
        <PaginationContainer $darkMode={darkMode}>
          <Pagination 
            count={pageCount} 
            page={page} 
            onChange={(e, value) => setPage(value)}
            color="primary"
          />
        </PaginationContainer>
      )}

      <DetailDialog
        open={dialogOpen}
        onClose={() => setDialogOpen(false)}
        maxWidth="md"
        fullWidth
        $darkMode={darkMode}
      >
        {selectedStore && (
          <>
            <DialogTitle>
              <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                <Typography variant="h6">{selectedStore.name}</Typography>
                {selectedStore.rating && (
                  <Rating value={selectedStore.rating} precision={0.5} readOnly />
                )}
              </Box>
            </DialogTitle>
            <DialogContent>
              <List>
                <ListItem>
                  <ListItemIcon>
                    <LocationOnIcon />
                  </ListItemIcon>
                  <ListItemText 
                    primary="Address"
                    secondary={selectedStore.address}
                  />
                </ListItem>
                {selectedStore.phone && (
                  <ListItem>
                    <ListItemIcon>
                      <PhoneIcon />
                    </ListItemIcon>
                    <ListItemText 
                      primary="Phone"
                      secondary={selectedStore.phone}
                    />
                  </ListItem>
                )}
                {selectedStore.hours && (
                  <ListItem>
                    <ListItemIcon>
                      <AccessTimeIcon />
                    </ListItemIcon>
                    <ListItemText 
                      primary="Hours"
                      secondary={selectedStore.hours}
                    />
                  </ListItem>
                )}
                {selectedStore.categories.length > 0 && (
                  <ListItem>
                    <ListItemIcon>
                      <StorefrontIcon />
                    </ListItemIcon>
                    <ListItemText 
                      primary="Categories"
                      secondary={selectedStore.categories.join(', ')}
                    />
                  </ListItem>
                )}
              </List>
            </DialogContent>
            <DialogActions>
              {selectedStore.phone && (
                <Button 
                  onClick={() => handleCall(selectedStore)}
                  startIcon={<PhoneIcon />}
                >
                  Call
                </Button>
              )}
              <Button
                variant="contained"
                onClick={() => handleGetDirections(selectedStore)}
                startIcon={<DirectionsIcon />}
              >
                Get Directions
              </Button>
            </DialogActions>
          </>
        )}
      </DetailDialog>
    </StoreContainer>
  );
};

export default PetStoreLocator; 