import React from 'react';
import {
  Box,
  Paper,
  Typography,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  Accordion,
  AccordionSummary,
  AccordionDetails,
} from '@mui/material';
import styled from 'styled-components';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import TipsAndUpdatesIcon from '@mui/icons-material/TipsAndUpdates';
import WaterDropIcon from '@mui/icons-material/WaterDrop';
import AcUnitIcon from '@mui/icons-material/AcUnit';
import WbSunnyIcon from '@mui/icons-material/WbSunny';
import PetsIcon from '@mui/icons-material/Pets';
import RestaurantIcon from '@mui/icons-material/Restaurant';
import CleaningServicesIcon from '@mui/icons-material/CleaningServices';
import FavoriteIcon from '@mui/icons-material/Favorite';

const CareContainer = styled(Paper)`
  padding: 2rem;
  margin: 0;
  background: ${props => props.$darkMode ? 'rgba(45, 45, 45, 0.9)' : 'rgba(255, 255, 255, 0.9)'};
  border-radius: 15px;
  backdrop-filter: blur(10px);
`;

const StyledAccordion = styled(Accordion)`
  background: ${props => props.$darkMode ? 'rgba(60, 60, 60, 0.9)' : 'rgba(255, 255, 255, 0.9)'};
  margin-bottom: 1rem;
  border: 1px solid ${props => props.$darkMode ? 'rgba(255, 255, 255, 0.1)' : 'rgba(0, 0, 0, 0.1)'};
  
  &:last-child {
    margin-bottom: 0;
  }
  
  &:before {
    display: none;
  }

  .MuiAccordionSummary-content {
    color: ${props => props.$darkMode ? '#fff' : '#333'};
  }

  .MuiAccordionDetails-root {
    color: ${props => props.$darkMode ? '#fff' : '#333'};
  }

  svg {
    color: ${props => props.$darkMode ? '#89CFF0' : '#5B8FA8'};
  }
`;

const getDefaultTips = () => [
  {
    category: 'Daily Care Essentials',
    icon: <PetsIcon />,
    items: [
      "Brush your cat's fur daily to prevent matting",
      "Check and trim claws when needed",
      "Provide fresh water daily",
      "Monitor litter box cleanliness"
    ]
  },
  {
    category: 'Nutrition Guidelines',
    icon: <RestaurantIcon />,
    items: [
      "Feed high-quality cat food appropriate for age",
      "Maintain regular feeding schedule",
      "Monitor portion sizes to prevent obesity",
      "Consult vet about dietary requirements"
    ]
  },
  {
    category: 'Health & Hygiene',
    icon: <CleaningServicesIcon />,
    items: [
      "Schedule regular vet check-ups",
      "Keep vaccinations up to date",
      "Clean food and water bowls regularly",
      "Watch for changes in behavior or appetite"
    ]
  },
  {
    category: 'Mental Wellbeing',
    icon: <FavoriteIcon />,
    items: [
      "Provide interactive toys for stimulation",
      "Create climbing and scratching spaces",
      "Spend quality time playing daily",
      "Maintain a consistent routine"
    ]
  }
];

const getWeatherSpecificTips = (weather) => {
  if (!weather?.current?.temp || !weather?.current?.weather?.[0]?.main) {
    return [];
  }

  const temp = weather.current.temp;
  const condition = weather.current.weather[0].main.toLowerCase();
  const tips = [];

  // Temperature-based tips
  if (temp > 30) {
    tips.push({
      category: 'Heat Safety',
      icon: <WbSunnyIcon />,
      items: [
        "Provide multiple water stations",
        "Create cool resting spots",
        "Watch for signs of heatstroke",
        "Limit outdoor activities"
      ]
    });
  } else if (temp < 5) {
    tips.push({
      category: 'Cold Weather Care',
      icon: <AcUnitIcon />,
      items: [
        "Keep cats indoors",
        "Provide warm sleeping areas",
        "Monitor hydration",
        "Check paws for ice/salt damage"
      ]
    });
  }

  // Weather condition tips
  switch (condition) {
    case 'rain':
      tips.push({
        category: 'Rainy Day Care',
        icon: <WaterDropIcon />,
        items: [
          "Dry paws after outdoor access",
          "Keep indoor litter boxes clean",
          "Monitor for dampness",
          "Provide cozy indoor spaces"
        ]
      });
      break;
    case 'snow':
      tips.push({
        category: 'Snow Safety',
        icon: <AcUnitIcon />,
        items: [
          "Check paws for snow buildup",
          "Maintain indoor temperature",
          "Increase caloric intake if needed",
          "Protect from drafts"
        ]
      });
      break;
    case 'clear':
      if (temp > 20) {
        tips.push({
          category: 'Sunny Day Care',
          icon: <WbSunnyIcon />,
          items: [
            "Provide shade options",
            "Ensure fresh water access",
            "Monitor sun exposure",
            "Create cool resting spots"
          ]
        });
      }
      break;
    default:
      // No specific tips for other weather conditions
      break;
  }

  return tips;
};

const CatCareTips = ({ weather, darkMode }) => {
  const defaultTips = getDefaultTips();
  const weatherTips = getWeatherSpecificTips(weather);
  const allTips = [...defaultTips, ...weatherTips];

  return (
    <CareContainer $darkMode={darkMode}>
      <Typography variant="h4" gutterBottom sx={{ color: darkMode ? '#fff' : '#333', mb: 3 }}>
        Cat Care Guide
      </Typography>

      <Typography variant="h5" gutterBottom sx={{ color: darkMode ? '#fff' : '#333', mb: 2 }}>
        Weather-Specific Care Tips
      </Typography>
      
      {allTips.map((section, index) => (
        <StyledAccordion key={index} $darkMode={darkMode}>
          <AccordionSummary 
            expandIcon={<ExpandMoreIcon sx={{ color: darkMode ? '#89CFF0' : '#5B8FA8' }} />}
          >
            <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
              {section.icon}
              <Typography variant="h6">{section.category}</Typography>
            </Box>
          </AccordionSummary>
          <AccordionDetails>
            <List>
              {section.items.map((tip, tipIndex) => (
                <ListItem key={tipIndex}>
                  <ListItemIcon>
                    <TipsAndUpdatesIcon />
                  </ListItemIcon>
                  <ListItemText primary={tip} />
                </ListItem>
              ))}
            </List>
          </AccordionDetails>
        </StyledAccordion>
      ))}
    </CareContainer>
  );
}

export default CatCareTips; 