import React from 'react';
import { Box, IconButton, Tooltip, Typography } from '@mui/material';
import styled from 'styled-components';
import DirectionsIcon from '@mui/icons-material/Directions';
import PhoneIcon from '@mui/icons-material/Phone';
import InfoIcon from '@mui/icons-material/Info';
import AccessTimeIcon from '@mui/icons-material/AccessTime';
import StorefrontIcon from '@mui/icons-material/Storefront';
import { motion } from 'framer-motion';

const MasonryGrid = styled(Box)`
  columns: 1;
  column-gap: 16px;
  padding: 12px;
  width: 100%;
  
  @media (min-width: 600px) {
    columns: 2;
  }
  
  @media (min-width: 960px) {
    columns: 3;
  }
  
  @media (min-width: 1280px) {
    columns: 3;
  }
`;

const StoreCard = styled(motion.div)`
  break-inside: avoid;
  margin-bottom: 16px;
  position: relative;
  border-radius: 12px;
  overflow: hidden;
  cursor: pointer;
  background: ${props => props.$darkMode ? '#2d2d2d' : '#f5f5f5'};
  display: inline-block;
  width: 100%;
  
  &:hover {
    .overlay {
      opacity: 1;
    }
  }
`;

const StoreContainer = styled.div`
  position: relative;
  width: 100%;
  padding-top: 100%; /* 1:1 Aspect Ratio */
  background: ${props => props.$darkMode ? '#2d2d2d' : '#f0f0f0'};
`;

const StoreContent = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding: 12px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
`;

const StoreLogo = styled.div`
  width: 48px;
  height: 48px;
  border-radius: 50%;
  background: ${props => props.$darkMode ? '#3d3d3d' : '#fff'};
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);

  svg {
    font-size: 24px;
    color: ${props => props.$darkMode ? '#89CFF0' : '#5B8FA8'};
  }
`;

const Overlay = styled(Box)`
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0.2) 0%,
    rgba(0, 0, 0, 0.7) 100%
  );
  opacity: 0;
  transition: opacity 0.3s ease;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 12px;
  color: white;
`;

const ActionButtons = styled(Box)`
  display: flex;
  gap: 4px;
  justify-content: flex-end;
`;

const StoreInfo = styled(Box)`
  text-align: left;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.5);
  max-width: 100%;
  overflow: hidden;
`;

const StorePhotoGrid = ({ stores, onSelect, onGetDirections, onCall, darkMode }) => {
  return (
    <MasonryGrid>
      {stores.map((store) => (
        <StoreCard
          key={store.id}
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.5 }}
          onClick={() => onSelect(store)}
          $darkMode={darkMode}
        >
          <StoreContainer $darkMode={darkMode}>
            <StoreContent>
              <StoreLogo $darkMode={darkMode}>
                <StorefrontIcon />
              </StoreLogo>
              <Typography 
                variant="subtitle1" 
                sx={{ 
                  color: darkMode ? '#fff' : '#333',
                  mb: 0.5,
                  fontWeight: 600,
                  fontSize: '0.9rem',
                  lineHeight: 1.2,
                  maxWidth: '100%',
                  overflow: 'hidden',
                  textOverflow: 'ellipsis',
                  whiteSpace: 'nowrap'
                }}
              >
                {store.name}
              </Typography>
              <Typography 
                variant="body2" 
                sx={{ 
                  color: darkMode ? '#ccc' : '#666',
                  fontSize: '0.8rem',
                  maxWidth: '100%',
                  overflow: 'hidden',
                  textOverflow: 'ellipsis',
                  whiteSpace: 'nowrap'
                }}
              >
                {store.distance}km away
              </Typography>
              {store.status && (
                <Typography 
                  variant="body2" 
                  sx={{ 
                    color: store.status.includes('Open') ? '#4caf50' : '#f44336',
                    mt: 0.5,
                    fontSize: '0.75rem',
                    maxWidth: '100%',
                    overflow: 'hidden',
                    textOverflow: 'ellipsis',
                    whiteSpace: 'nowrap'
                  }}
                >
                  {store.status}
                </Typography>
              )}
            </StoreContent>
            <Overlay className="overlay">
              <ActionButtons>
                <Tooltip title="Get Directions">
                  <IconButton
                    size="small"
                    onClick={(e) => {
                      e.stopPropagation();
                      onGetDirections(store);
                    }}
                    sx={{ 
                      color: 'white',
                      background: 'rgba(0, 0, 0, 0.3)',
                      padding: '4px',
                      '&:hover': {
                        background: 'rgba(0, 0, 0, 0.5)'
                      }
                    }}
                  >
                    <DirectionsIcon sx={{ fontSize: '1.2rem' }} />
                  </IconButton>
                </Tooltip>
                {store.phone && (
                  <Tooltip title="Call Store">
                    <IconButton
                      size="small"
                      onClick={(e) => {
                        e.stopPropagation();
                        onCall(store);
                      }}
                      sx={{ 
                        color: 'white',
                        background: 'rgba(0, 0, 0, 0.3)',
                        padding: '4px',
                        '&:hover': {
                          background: 'rgba(0, 0, 0, 0.5)'
                        }
                      }}
                    >
                      <PhoneIcon sx={{ fontSize: '1.2rem' }} />
                    </IconButton>
                  </Tooltip>
                )}
                <Tooltip title="Store Details">
                  <IconButton
                    size="small"
                    sx={{ 
                      color: 'white',
                      background: 'rgba(0, 0, 0, 0.3)',
                      padding: '4px',
                      '&:hover': {
                        background: 'rgba(0, 0, 0, 0.5)'
                      }
                    }}
                  >
                    <InfoIcon sx={{ fontSize: '1.2rem' }} />
                  </IconButton>
                </Tooltip>
              </ActionButtons>
              <StoreInfo>
                <Typography 
                  variant="subtitle2" 
                  sx={{ 
                    mb: 0.5, 
                    fontWeight: 600,
                    fontSize: '0.8rem',
                    lineHeight: 1.2,
                    maxWidth: '100%',
                    overflow: 'hidden',
                    textOverflow: 'ellipsis',
                    whiteSpace: 'nowrap'
                  }}
                >
                  {store.name}
                </Typography>
                <Typography 
                  variant="body2" 
                  sx={{ 
                    fontSize: '0.75rem',
                    lineHeight: 1.2,
                    maxWidth: '100%',
                    overflow: 'hidden',
                    textOverflow: 'ellipsis',
                    display: '-webkit-box',
                    WebkitLineClamp: 2,
                    WebkitBoxOrient: 'vertical',
                    whiteSpace: 'normal'
                  }}
                >
                  {store.address}
                </Typography>
                {store.hours && (
                  <Box sx={{ display: 'flex', alignItems: 'center', gap: 0.5, mt: 0.5 }}>
                    <AccessTimeIcon sx={{ fontSize: '0.8rem', flexShrink: 0 }} />
                    <Typography 
                      variant="body2" 
                      sx={{ 
                        fontSize: '0.75rem',
                        lineHeight: 1.2,
                        maxWidth: '100%',
                        overflow: 'hidden',
                        textOverflow: 'ellipsis',
                        whiteSpace: 'nowrap'
                      }}
                    >
                      {store.hours}
                    </Typography>
                  </Box>
                )}
              </StoreInfo>
            </Overlay>
          </StoreContainer>
        </StoreCard>
      ))}
    </MasonryGrid>
  );
};

export default StorePhotoGrid; 