import React, { useState } from 'react';
import { Paper, Typography, Grid, Switch, Box } from '@mui/material';
import styled, { keyframes } from 'styled-components';
import WbSunnyIcon from '@mui/icons-material/WbSunny';
import AcUnitIcon from '@mui/icons-material/AcUnit';
import CloudIcon from '@mui/icons-material/Cloud';
import BeachAccessIcon from '@mui/icons-material/BeachAccess';
import AirIcon from '@mui/icons-material/Air';
import WbTwilightIcon from '@mui/icons-material/WbTwilight';
import CompressIcon from '@mui/icons-material/Compress';
import VisibilityIcon from '@mui/icons-material/Visibility';
import LocationOnIcon from '@mui/icons-material/LocationOn';
import MeowAlerts from '../features/weather/MeowAlerts';
import CatCareTips from '../features/care/CatCareTips';
import PetStoreLocator from '../features/stores/PetStoreLocator';

const colors = {
  babyBlue: '#89CFF0',
  lightBlue: '#BFE6FF',
  darkBlue: '#5B8FA8',
  white: '#FFFFFF',
  black: '#333333'
};

const float = keyframes`
  0% { transform: translateY(0px); }
  50% { transform: translateY(-10px); }
  100% { transform: translateY(0px); }
`;

const rotate = keyframes`
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
`;

const WeatherCard = styled(Paper)`
  padding: 2rem;
  margin: 1rem 0;
  background: ${props => props.$darkMode ? 'rgba(45, 45, 45, 0.9)' : 'rgba(255, 255, 255, 0.9)'};
  backdrop-filter: blur(10px);
  border-radius: 15px;
  border: 1px solid ${props => props.$darkMode ? 'transparent' : colors.babyBlue};
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
  box-shadow: 0 12px 20px ${props => props.$darkMode
    ? 'rgba(0, 0, 0, 0.3)'
    : 'rgba(137, 207, 240, 0.3)'};

  &:hover {
    transform: translateY(-5px);
  }
`;

const WeatherIcon = styled.div`
  font-size: 4rem;
  margin-bottom: 1rem;
  color: ${props => props.$darkMode ? colors.lightBlue : colors.babyBlue};
  animation: ${float} 3s ease-in-out infinite;
  filter: drop-shadow(0 4px 8px ${props => props.$darkMode
    ? 'rgba(0, 0, 0, 0.3)'
    : 'rgba(137, 207, 240, 0.3)'});
`;

const WindIcon = styled(AirIcon)`
  animation: ${rotate} ${props => props.$windSpeed ? `${20 / props.$windSpeed}s` : '20s'} linear infinite;
  color: ${props => props.$darkMode ? colors.lightBlue : colors.babyBlue};
`;

const DetailBox = styled(Box)`
  display: flex;
  align-items: center;
  gap: 1rem;
  margin-bottom: 1rem;
  padding: 1rem;
  border-radius: 10px;
  background: ${props => props.$darkMode ? 'rgba(45, 45, 45, 0.5)' : 'rgba(255, 255, 255, 0.5)'};
  transition: all 0.3s ease;

  &:hover {
    transform: translateX(5px);
    background: ${props => props.$darkMode ? 'rgba(45, 45, 45, 0.8)' : 'rgba(255, 255, 255, 0.8)'};
  }

  svg {
    font-size: 1.5rem;
    color: ${props => props.$darkMode ? colors.lightBlue : colors.babyBlue};
  }
`;

const StyledWeatherIcon = styled(Box)`
  color: ${props => props.$darkMode ? colors.lightBlue : colors.babyBlue};
  font-size: inherit;
  display: flex;
  align-items: center;
  justify-content: center;
`;

const getWeatherIcon = (condition, darkMode) => {
  const iconProps = {
    fontSize: "inherit",
    sx: { color: 'inherit' }
  };

  switch (condition?.toLowerCase()) {
    case 'clear':
      return (
        <StyledWeatherIcon $darkMode={darkMode}>
          <WbSunnyIcon {...iconProps} />
        </StyledWeatherIcon>
      );
    case 'snow':
      return (
        <StyledWeatherIcon $darkMode={darkMode}>
          <AcUnitIcon {...iconProps} />
        </StyledWeatherIcon>
      );
    case 'rain':
      return (
        <StyledWeatherIcon $darkMode={darkMode}>
          <BeachAccessIcon {...iconProps} />
        </StyledWeatherIcon>
      );
    case 'clouds':
      return (
        <StyledWeatherIcon $darkMode={darkMode}>
          <CloudIcon {...iconProps} />
        </StyledWeatherIcon>
      );
    default:
      return (
        <StyledWeatherIcon $darkMode={darkMode}>
          <WbSunnyIcon {...iconProps} />
        </StyledWeatherIcon>
      );
  }
};

const celsiusToFahrenheit = (celsius) => (celsius * 9/5) + 32;

const formatTime = (timestamp) => {
  return new Date(timestamp * 1000).toLocaleTimeString([], { 
    hour: '2-digit', 
    minute: '2-digit' 
  });
};

const getCatAdvice = (condition, temp, time) => {
  const isNight = time && (new Date(time * 1000).getHours() < 6 || new Date(time * 1000).getHours() > 20);
  
  const advice = {
    clear: {
      day: {
        hot: "Find a cool spot in the shade, and don't forget to drink water! 🐱💧",
        normal: "Perfect weather for a catnap in the sunbeam! 😺☀️",
        cold: "Find that sunny windowsill and soak up the warmth! 😸✨"
      },
      night: {
        hot: "Night prowling weather! Keep hydrated! 🐱🌙",
        normal: "Purr-fect night for stargazing! 😺⭐",
        cold: "Time to snuggle under the blankets! 😸🛏️"
      }
    },
    clouds: {
      day: {
        hot: "Good day for a lazy indoor stretch! 🐱🧘‍♂️",
        normal: "Ideal weather for window bird watching! 😺🐦",
        cold: "Time to find a warm lap to sit on! 😸💝"
      },
      night: {
        hot: "Nice evening for roof adventures! 🐱🏠",
        normal: "Cozy night for indoor zoomies! 😺🏃‍♂️",
        cold: "Cuddle up in your favorite blanket! 😸🛋️"
      }
    },
    rain: {
      day: {
        hot: "Watch the rain from your favorite windowsill! 🐱🌧️",
        normal: "Perfect day for indoor acrobatics! 😺🎪",
        cold: "Time for a warm radiator nap! 😸🔥"
      },
      night: {
        hot: "Stay dry and watch the lightning show! 🐱⚡",
        normal: "Cozy night for purring under covers! 😺🛏️",
        cold: "Extra snuggles required tonight! 😸❤️"
      }
    }
  };

  const getTemperatureRange = (temp) => {
    if (temp > 25) return 'hot';
    if (temp < 10) return 'cold';
    return 'normal';
  };

  const timeOfDay = isNight ? 'night' : 'day';
  const tempRange = getTemperatureRange(temp);
  const weatherKey = condition?.toLowerCase() || 'clear';

  return advice[weatherKey]?.[timeOfDay]?.[tempRange] || 
    "Whatever the weather, it's always time for a cat nap! 😺💤";
};

const CatAdviceBox = styled(Box)`
  margin-top: 20px;
  padding: 15px;
  border-radius: 12px;
  background: ${props => props.$darkMode 
    ? 'rgba(45, 45, 45, 0.9)' 
    : 'rgba(137, 207, 240, 0.1)'};
  border: 1px solid ${props => props.$darkMode ? colors.darkBlue : colors.babyBlue};
  transition: all 0.3s ease;

  &:hover {
    transform: translateX(5px);
    background: ${props => props.$darkMode 
      ? 'rgba(45, 45, 45, 0.95)' 
      : 'rgba(137, 207, 240, 0.15)'};
  }
`;

const CareContainer = styled(Paper)`
  padding: 2rem;
  margin: 1rem 0;
  background: ${props => props.$darkMode ? 'rgba(45, 45, 45, 0.9)' : 'rgba(255, 255, 255, 0.9)'};
  border-radius: 15px;
  backdrop-filter: blur(10px);
`;

const WeatherDisplay = ({ weather, darkMode }) => {
  const [showCelsius, setShowCelsius] = useState(true);
  
  // Debug log to check incoming weather data
  console.log('Raw weather data:', JSON.stringify(weather, null, 2));

  if (!weather?.current) {
    console.log('No weather data available');
    return null;
  }

  // Extract weather data with proper type checking
  const {
    current: {
      temp,
      feels_like,
      humidity,
      pressure,
      wind_speed,
      weather: [{ main = '', description = '' }] = [{}],
      visibility,
      sunrise: unixSunrise,
      sunset: unixSunset
    },
    name = 'Unknown Location',
    sys: { country = '' } = {}
  } = weather;

  // Format sunrise and sunset times
  const sunrise = unixSunrise ? formatTime(unixSunrise) : 'N/A';
  const sunset = unixSunset ? formatTime(unixSunset) : 'N/A';

  // Format location display
  const locationDisplay = country ? `${name}, ${country}` : name;

  return (
    <Grid container spacing={2}>
      <Grid item xs={12}>
        <WeatherCard $darkMode={darkMode}>
          <Box sx={{ display: 'flex', flexDirection: 'column', gap: 1, mb: 2 }}>
            <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
              <Typography variant="h4" sx={{ color: darkMode ? colors.white : colors.black }}>
                Current Weather
              </Typography>
              <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                <Typography>°F</Typography>
                <Switch
                  checked={showCelsius}
                  onChange={() => setShowCelsius(!showCelsius)}
                  color="primary"
                />
                <Typography>°C</Typography>
              </Box>
            </Box>
            <Typography 
              variant="h6" 
              sx={{ 
                color: darkMode ? colors.lightBlue : colors.darkBlue,
                display: 'flex',
                alignItems: 'center',
                gap: 1,
                mt: 1
              }}
            >
              <LocationOnIcon sx={{ fontSize: '1.2rem' }} />
              {locationDisplay}
            </Typography>
          </Box>
          
          <WeatherIcon $darkMode={darkMode}>
            {getWeatherIcon(main, darkMode)}
          </WeatherIcon>

          <Typography variant="h2" sx={{ mb: 2, color: darkMode ? colors.white : colors.black }}>
            {showCelsius ? `${temp.toFixed(1)}°C` : `${celsiusToFahrenheit(temp).toFixed(1)}°F`}
          </Typography>

          <Typography variant="h6" sx={{ mb: 3, color: darkMode ? colors.lightBlue : colors.darkBlue }}>
            {description.charAt(0).toUpperCase() + description.slice(1)}
          </Typography>

          <Grid container spacing={2}>
            <Grid item xs={12} sm={6}>
              <DetailBox $darkMode={darkMode}>
                <WbTwilightIcon />
                <Box>
                  <Typography variant="body2" sx={{ color: darkMode ? colors.lightBlue : colors.darkBlue }}>
                    Sunrise
                  </Typography>
                  <Typography variant="body1" sx={{ color: darkMode ? colors.white : colors.black }}>
                    {sunrise}
                  </Typography>
                </Box>
              </DetailBox>
            </Grid>

            <Grid item xs={12} sm={6}>
              <DetailBox $darkMode={darkMode}>
                <WbTwilightIcon />
                <Box>
                  <Typography variant="body2" sx={{ color: darkMode ? colors.lightBlue : colors.darkBlue }}>
                    Sunset
                  </Typography>
                  <Typography variant="body1" sx={{ color: darkMode ? colors.white : colors.black }}>
                    {sunset}
                  </Typography>
                </Box>
              </DetailBox>
            </Grid>

            <Grid item xs={12} sm={6}>
              <DetailBox $darkMode={darkMode}>
                <WindIcon $windSpeed={wind_speed} $darkMode={darkMode} />
                <Box>
                  <Typography variant="body2" sx={{ color: darkMode ? colors.lightBlue : colors.darkBlue }}>
                    Wind Speed
                  </Typography>
                  <Typography variant="body1" sx={{ color: darkMode ? colors.white : colors.black }}>
                    {wind_speed} m/s
                  </Typography>
                </Box>
              </DetailBox>
            </Grid>

            <Grid item xs={12} sm={6}>
              <DetailBox $darkMode={darkMode}>
                <CompressIcon />
                <Box>
                  <Typography variant="body2" sx={{ color: darkMode ? colors.lightBlue : colors.darkBlue }}>
                    Pressure
                  </Typography>
                  <Typography variant="body1" sx={{ color: darkMode ? colors.white : colors.black }}>
                    {pressure} hPa
                  </Typography>
                </Box>
              </DetailBox>
            </Grid>

            <Grid item xs={12} sm={6}>
              <DetailBox $darkMode={darkMode}>
                <VisibilityIcon />
                <Box>
                  <Typography variant="body2" sx={{ color: darkMode ? colors.lightBlue : colors.darkBlue }}>
                    Visibility
                  </Typography>
                  <Typography variant="body1" sx={{ color: darkMode ? colors.white : colors.black }}>
                    {(visibility / 1000).toFixed(1)} km
                  </Typography>
                </Box>
              </DetailBox>
            </Grid>
          </Grid>
        </WeatherCard>
      </Grid>

      <Grid item xs={12}>
        <MeowAlerts weather={weather} darkMode={darkMode} />
      </Grid>

      <Grid item xs={12}>
        <CatCareTips weather={weather} darkMode={darkMode} />
      </Grid>

      <Grid item xs={12}>
        <PetStoreLocator weather={weather} darkMode={darkMode} />
      </Grid>
    </Grid>
  );
};

export default WeatherDisplay; 