import React from 'react';
import {
  Box,
  Paper,
  Typography,
  Alert,
  AlertTitle,
  Collapse
} from '@mui/material';
import styled from 'styled-components';

const AlertsContainer = styled(Paper)`
  padding: 1.5rem;
  margin: 1rem 0;
  background: ${props => props.$darkMode ? 'rgba(45, 45, 45, 0.9)' : 'rgba(255, 255, 255, 0.9)'};
  border-radius: 15px;
  backdrop-filter: blur(10px);
`;

const getAlertSeverity = (condition) => {
  switch (condition.toLowerCase()) {
    case 'thunderstorm':
    case 'tornado':
      return 'error';
    case 'rain':
    case 'snow':
      return 'warning';
    case 'clouds':
      return 'info';
    default:
      return 'success';
  }
};

const getCatAdvice = (condition, temperature, humidity, windSpeed) => {
  const alerts = [];

  // Temperature-based alerts
  if (temperature > 30) {
    alerts.push({
      severity: 'warning',
      title: 'Hot Weather Alert',
      message: 'Keep your furry friends cool! Ensure fresh water and shady spots are available. Watch for signs of heatstroke in your cats.'
    });
  } else if (temperature < 0) {
    alerts.push({
      severity: 'warning',
      title: 'Cold Weather Alert',
      message: 'Paw-lease keep your cats warm! Consider limiting outdoor time and providing warm, cozy spots indoors.'
    });
  }

  // Weather condition alerts
  switch (condition.toLowerCase()) {
    case 'thunderstorm':
      alerts.push({
        severity: 'error',
        title: 'Thunder Warning',
        message: 'Cats may be stressed by thunder. Create a safe, quiet space and consider using calming aids.'
      });
      break;
    case 'rain':
      alerts.push({
        severity: 'info',
        title: 'Rainy Day Notice',
        message: 'Keep towels ready for wet paws! Indoor enrichment activities recommended.'
      });
      break;
    case 'snow':
      alerts.push({
        severity: 'warning',
        title: 'Snow Advisory',
        message: 'Check paws for snow buildup and salt exposure. Consider indoor litter box options.'
      });
      break;
    case 'clear':
      if (temperature > 20) {
        alerts.push({
          severity: 'success',
          title: 'Purr-fect Weather',
          message: 'Great day for supervised outdoor activities! Remember sun protection for light-colored cats.'
        });
      }
      break;
  }

  // Humidity alerts
  if (humidity > 80) {
    alerts.push({
      severity: 'info',
      title: 'High Humidity Alert',
      message: 'High humidity can affect cats with respiratory issues. Monitor breathing and ensure good ventilation.'
    });
  }

  // Wind alerts
  if (windSpeed > 30) {
    alerts.push({
      severity: 'warning',
      title: 'High Wind Advisory',
      message: 'Strong winds can spook cats. Secure outdoor items and consider keeping cats indoors.'
    });
  }

  return alerts;
};

const MeowAlerts = ({ weather, darkMode }) => {
  if (!weather?.current?.weather?.[0]?.main || !weather?.current?.temp) {
    return null;
  }

  const alerts = getCatAdvice(
    weather.current.weather[0].main,
    weather.current.temp,
    weather.current.humidity || 0,
    weather.current.wind_speed || 0
  );

  return (
    <AlertsContainer $darkMode={darkMode}>
      <Typography variant="h6" gutterBottom sx={{ color: darkMode ? '#fff' : '#333' }}>
        Meow-teorological Alerts 🐱
      </Typography>
      
      <Box sx={{ mt: 2 }}>
        {alerts.map((alert, index) => (
          <Collapse in={true} key={index}>
            <Alert 
              severity={alert.severity}
              sx={{ 
                mb: 2,
                backgroundColor: darkMode ? 'rgba(0, 0, 0, 0.2)' : 'inherit',
                color: darkMode ? '#fff' : 'inherit',
                '& .MuiAlert-icon': {
                  color: darkMode ? '#fff' : 'inherit'
                }
              }}
            >
              <AlertTitle>{alert.title}</AlertTitle>
              {alert.message}
            </Alert>
          </Collapse>
        ))}
        
        {alerts.length === 0 && (
          <Alert 
            severity="success"
            sx={{ 
              backgroundColor: darkMode ? 'rgba(0, 0, 0, 0.2)' : 'inherit',
              color: darkMode ? '#fff' : 'inherit'
            }}
          >
            <AlertTitle>Purr-fect Weather!</AlertTitle>
            No special precautions needed - just regular cat care and love! 😺
          </Alert>
        )}
      </Box>
    </AlertsContainer>
  );
};

export default MeowAlerts; 