const FOURSQUARE_API_KEY = 'fsq3ex1cKUKBWMDbCwLf5RciyWSP7IxdUe3Li1fV9/JcQu8=';
const BASE_URL = 'https://api.foursquare.com/v3';

export const searchPetStores = async (latitude, longitude) => {
  try {
    // First, try to search specifically for pet stores
    const searchParams = new URLSearchParams({
      ll: `${latitude},${longitude}`,
      radius: 20000, // 20km radius
      query: 'pet store OR petco OR petsmart OR pet supplies',
      sort: 'DISTANCE',
      limit: 50,
      fields: 'fsq_id,name,location,distance,categories,geocodes,hours,rating,website,tel,description'
    });

    const response = await fetch(`${BASE_URL}/places/search?${searchParams}`, {
      method: 'GET',
      headers: {
        'Accept': 'application/json',
        'Authorization': FOURSQUARE_API_KEY
      }
    });

    if (!response.ok) {
      const errorData = await response.json().catch(() => ({}));
      console.error('Foursquare API Error:', errorData);
      throw new Error(`Failed to fetch stores: ${response.status} ${response.statusText}`);
    }

    const data = await response.json();
    
    // Filter results to only include pet-related stores
    const petStores = data.results.filter(store => {
      const name = store.name.toLowerCase();
      const isPetStore = name.includes('pet') || 
                        name.includes('animal') || 
                        name.includes('petsmart') || 
                        name.includes('petco') ||
                        name.includes('aquarium');
      return isPetStore;
    });

    return { ...data, results: petStores };
  } catch (error) {
    console.error('Error fetching stores:', error);
    throw error;
  }
};

export const getStoreDetails = async (fsqId) => {
  try {
    const response = await fetch(`${BASE_URL}/places/${fsqId}?fields=hours,website,rating,tel,description,photos`, {
      method: 'GET',
      headers: {
        'Accept': 'application/json',
        'Authorization': FOURSQUARE_API_KEY
      }
    });

    if (!response.ok) {
      const errorData = await response.json().catch(() => ({}));
      console.error('Foursquare API Error:', errorData);
      throw new Error(`Failed to fetch store details: ${response.status} ${response.statusText}`);
    }

    const data = await response.json();
    return data;
  } catch (error) {
    console.error('Error fetching store details:', error);
    throw error;
  }
}; 