import React, { useState, useEffect } from 'react';
import { ThemeProvider } from '@mui/material/styles';
import { Container, IconButton, Box, Typography, useMediaQuery } from '@mui/material';
import Brightness4Icon from '@mui/icons-material/Brightness4';
import Brightness7Icon from '@mui/icons-material/Brightness7';
import styled from 'styled-components';
import WeatherDisplay from './components/WeatherDisplay';
import SearchBar from './components/SearchBar';
import { fetchWeather } from './services/weatherService';
import { theme } from './theme';
import WeatherCatLogo from './components/WeatherCatLogo';

const colors = {
  babyBlue: '#89CFF0',
  lightBlue: '#BFE6FF',
  darkBlue: '#5B8FA8',
  white: '#FFFFFF',
  black: '#333333',
  darkMode: {
    background: '#1A1A1A',
    surface: '#2D2D2D'
  }
};

const MainContainer = styled(Container)`
  padding: 1rem;
  min-height: 100vh;
  width: 100%;
  max-width: 100%;
  margin: 0;
  background: ${props => getWeatherBackground(props.$weather, props.$darkMode)};
  transition: background 0.3s ease;

  @media (max-width: 600px) {
    padding: 0.5rem;
  }
`;

const Header = styled(Box)`
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  margin-bottom: 1rem;
  border-radius: 15px;
  background: ${props => props.$darkMode ? 'rgba(45, 45, 45, 0.9)' : 'rgba(255, 255, 255, 0.9)'};
  backdrop-filter: blur(10px);
  box-shadow: 0 4px 6px ${props => props.$darkMode ? 'rgba(0, 0, 0, 0.2)' : 'rgba(137, 207, 240, 0.2)'};

  @media (max-width: 600px) {
    padding: 0.75rem;
    margin-bottom: 0.5rem;
  }
`;

const getWeatherBackground = (weather, isDarkMode) => {
  if (!weather?.current?.weather[0]?.main) return null;
  
  const condition = weather.current.weather[0].main.toLowerCase();
  const timeOfDay = new Date().getHours();
  const isNight = timeOfDay < 6 || timeOfDay > 18;

  if (isDarkMode) {
    return {
      clear: `linear-gradient(135deg, #1A1A1A 0%, ${isNight ? '#000B18' : '#2D2D2D'} 100%)`,
      clouds: 'linear-gradient(135deg, #1A1A1A 0%, #2D2D2D 100%)',
      rain: 'linear-gradient(135deg, #1A1A1A 0%, #1E2A3B 100%)',
      snow: 'linear-gradient(135deg, #1A1A1A 0%, #2C3440 100%)',
      thunderstorm: 'linear-gradient(135deg, #1A1A1A 0%, #1E1E2E 100%)',
      default: `linear-gradient(135deg, ${colors.darkMode.background} 0%, ${colors.darkMode.surface} 100%)`
    }[condition] || 'linear-gradient(135deg, #1A1A1A 0%, #2D2D2D 100%)';
  }

  return {
    clear: `linear-gradient(135deg, ${isNight ? '#1A237E' : '#89CFF0'} 0%, ${isNight ? '#283593' : '#BFE6FF'} 100%)`,
    clouds: 'linear-gradient(135deg, #90A4AE 0%, #CFD8DC 100%)',
    rain: 'linear-gradient(135deg, #78909C 0%, #B0BEC5 100%)',
    snow: 'linear-gradient(135deg, #E3F2FD 0%, #FFFFFF 100%)',
    thunderstorm: 'linear-gradient(135deg, #455A64 0%, #78909C 100%)',
    default: `linear-gradient(135deg, ${colors.babyBlue} 0%, ${colors.lightBlue} 100%)`
  }[condition] || `linear-gradient(135deg, ${colors.babyBlue} 0%, ${colors.lightBlue} 100%)`;
};

function App() {
  const [darkMode, setDarkMode] = useState(false);
  const [weather, setWeather] = useState(null);
  const [city, setCity] = useState('London');
  const [error, setError] = useState(null);
  const [loading, setLoading] = useState(true);
  const prefersDarkMode = useMediaQuery('(prefers-color-scheme: dark)');

  useEffect(() => {
    setDarkMode(prefersDarkMode);
  }, [prefersDarkMode]);

  const getWeatherData = async (searchCity) => {
    try {
      setLoading(true);
      setError(null);
      const data = await fetchWeather(searchCity);
      setWeather(data);
    } catch (err) {
      setError('Failed to fetch weather data. Please try again.');
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    getWeatherData(city);
  }, [city]);

  const handleSearch = (searchCity) => {
    setCity(searchCity);
  };

  const toggleDarkMode = () => {
    setDarkMode(!darkMode);
  };

  return (
    <ThemeProvider theme={theme}>
      <MainContainer $weather={weather} $darkMode={darkMode}>
        <Header $darkMode={darkMode}>
          <WeatherCatLogo darkMode={darkMode} />
          <IconButton onClick={toggleDarkMode} color="inherit">
            {darkMode ? <Brightness7Icon /> : <Brightness4Icon />}
          </IconButton>
        </Header>

        <SearchBar onSearch={handleSearch} darkMode={darkMode} />

        {error && (
          <Typography color="error" align="center" gutterBottom>
            {error}
          </Typography>
        )}

        {loading ? (
          <Box display="flex" justifyContent="center" alignItems="center" minHeight="200px">
            <Typography>Loading weather data...</Typography>
          </Box>
        ) : (
          <WeatherDisplay weather={weather} darkMode={darkMode} />
        )}
      </MainContainer>
    </ThemeProvider>
  );
}

export default App; 